import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';

const Persons = ({ persons, handleDelete }) => {
    return (
        <div>
            {persons.map((props) => <Card style={{ padding: "10px" }} key={props.id}><Card.Body key={props.name}>{props.name} {props.number}</Card.Body><Button variant="danger" key={props.number} onClick={() => handleDelete(props)}>Delete</Button></Card>)}
        </div>
    )
}

export default Persons
