import Alert from 'react-bootstrap/Alert';

const Notification = ({ errormessage, message }) => {
    if (message === null && errormessage === null) {
        return null
    }
    return (
        <div>
            {errormessage !== null ? <Alert variant="danger" className="error">{errormessage}</Alert> : <div></div>}
            {message !== null ? <Alert variant="success" className="message">{message}</Alert> : <div></div>}
        </div>
    )
}

export default Notification